header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* =======CTA======= */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ============ HEADER SOCIALS =========== */
.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ===============ME ===============*/
.me img {
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 2rem;
  border-radius: 12rem 12rem 0 0;
  border: 2px solid #fff;
  transition: transform 0.3s ease; /* Add a transition for the transform property */
}

.me img:hover {
  transform: scale(1.1);
  border-color: var(--color-primary-variant);
}
.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ================MEDIA QUERIES (MEDIUM DEVICES ============= */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ================MEDIA QUERIES (SMALL DEVICES ============= */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header_socials,
  .scroll_down {
    display: none;
  }
}
@media screen and (max-width: 360px) {
  header {
    height: 125vh;
    width: 100vh;
  }
  .header_socials,
  .scroll_down {
    display: none;
  }
}